import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import { NavLink, useLocation, useNavigate } from 'react-router-dom';
import logo from '../../assets/logo.png'
import './style.css'
import { useState } from 'react';

function NavBar() {
  const [search, setSearch] = useState('')
  const navigate = useNavigate()
  const location = useLocation()

  return (
    <Navbar collapseOnSelect expand="lg" fixed="top" className="bg-body-tertiary py-2">
      <Container style={{direction: 'rtl'}}>
        <Navbar.Brand>
          <NavLink to="/">
            <img src={logo} alt="Logo" height='50px' />
          </NavLink>
        </Navbar.Brand>
        {(location?.pathname === "/high-pressure" || 
          location?.pathname === "/products" || 
          location?.pathname === "/preumium-products" || 
          location?.pathname?.includes('/categories/')
        ) && <form onSubmit={(e)=> {
          e.preventDefault()
          if(location?.pathname === "/high-pressure" || location?.pathname === "/products"){
            navigate('/products', {state: search})
          } else {
            navigate('/preumium-products', {state: search})
          }
        }}>
        <div style={{
          display: 'flex',
          alignItems: 'center',
          gap: '8px',
          direction: 'ltr',
          borderBottom: '1px solid #fff'
        }}>
          <button type='submit' style={{background: 'none', border: 'none'}}>
            <i className='la la-search'></i>
          </button>
          
          <input 
            type='text' 
            placeholder='Search Here'
            value={search}
            onChange={e=> setSearch(e.target?.value)}
            onEnter
          />
        </div>
        </form>}
        {/* <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="m-auto">
            <NavLink to="/" className='nav-link'>
                <i className="la la-arrow-left"></i>
            </NavLink>
            <NavLink to="/activities" className='nav-link'>
                 
            </NavLink>
            <NavLink to="/markets" className='nav-link'> </NavLink>
            <NavLink to="/offers" className='nav-link'> </NavLink>
            <NavLink to="/news" className='nav-link'> </NavLink>
            <NavLink to="/contact-us" className='nav-link'> </NavLink>
          </Nav>
        </Navbar.Collapse> */}
      </Container>
    </Navbar>
  );
}

export default NavBar;